.main-container{
    padding-right: 500px;
    box-sizing: border-box;
}

.content{
    display: flex;
    position: relative;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.gallery{
    position: relative;
    width: 250vw;
    display: grid;
    grid-template-columns: repeat(50, 2%);
    grid-template-rows: repeat(50,2%);
    padding-left: 12vw;
    height: 100%;
    text-align: center;
    background-color: #fff;
}

.gallery-helper{
    grid-area: 43/1/span 2/span 1;
    font-size: 20px;
}

.behind-text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 12em;
    font-weight: 500;
    letter-spacing: 50px;
}

.fill{
    color: rgba(0,0,0,0.2);
}

.outline{
    z-index: 2;
    mix-blend-mode: overlay;
}