/* Reset styles */
*,*::after,*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Global styles */
body {
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  background-color: #fff;
}

/* Main content container */
.container {
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}

.footer {
  height: 100%;
  background: #252a47;
  display: grid;
  place-items: center;
  width: 100vw;
  z-index: -1;
  background: #8e2de2; 
  background: -webkit-linear-gradient(
    to right,
    #4a00e0,
    #8e2de2
  ); 
  background: linear-gradient(
    to right,
    #4a00e0,
    #8e2de2
  );
}

.contact-addresses {
  width: 50vw;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10%;
  color: white;
}

.contact-addresses h1 {
  font-family: 'Oswald', sans-serif;
  font-size: 36px;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 30px;
  letter-spacing: 2px;
}

.contact-addresses p {
  margin: 10px 0 10px;
}

.navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 50px;
  box-sizing: border-box;
  z-index: 1;
  position: fixed;

  mix-blend-mode: difference;
  color: white;
}


.navbar ul {
  display: flex;
  list-style: none;
}

.navbar li {
  margin: 0 15px;
}

.gallery-item-image{
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  will-change: transform;
  clip-path: inset(0% 100% 0% 0%);
  transition: clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);
}

.gallery-item-imginner{
  background-position: center;
  background-origin: center left;
  background-size: cover;
  width: 100%;
  height: 100%;
  will-change: transform;
  transform: scale(1.1);
  transition: transform 2s cubic-bezier(0.77,0,0.175,1);
}

.reveal{
  clip-path: inset(0% 0% 0% 0%);
}

.reveal.gallery-item-imginner{
  transform: scale(1);
  transition: transform 1s cubic-bezier(0.77,0,0.175,1);
}